.terminal {
    padding-bottom: 20px;
}

.terminal-out {
    padding: 15px;
    background-color: #202020;
    color: white;
    width: 100%;
    height: 200px;
    font-size: 15px;
}

.terminal-item {
    margin: 10px;
    padding: 10px;
    width: calc(100% - 20px);
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 5px;
    height: 75px;

    -webkit-touch-callout: none;
    /*系统默认菜单被禁用*/
    -webkit-user-select: none;
    /*webkit浏览器*/
    -khtml-user-select: none;
    /*早期浏览器*/
    -moz-user-select: none;
    /*火狐*/
    -ms-user-select: none;
    /*IE10*/
    user-select: none;
}

.terminal-item:hover {
    box-shadow: 0 0 8px 0 rgba(0,0,0,0.3);
}

.terminal-text {
    display: inline-block;
    width: calc(100% - 100px);
    word-wrap:break-word;
}
.terminal-text-title {
    font-size: 20px;
    font-weight: bold;
}
.terminal-text-content {
    font-size: 15px;
}

.terminal-buttoncontainer {
    padding-left: 10px;
    border-left: solid;
    border-width: 1px;
    border-color: #51515140;
    float: right;
}
.terminal-button {
    display: block;
    font-weight: bold;
    padding-top: 8px;
    height: 55px;
    font-size: 25px;
}
.terminal-item-double {
    display: flex;
    align-items: center;
}
.terminal-imgbox {
    display: flex;
    width: 100%;
    height: 50px;
    flex-direction: column;
    align-items: center;
}
.terminal-imgbox-double {
    display: flex;
    width: 50%;
    height: 50px;
    flex-direction: column;
    align-items: center;
}
.terminal-imgbox-double-line {
    border-left: solid;
    border-width: 1px;
    border-color: #51515140;
}
.terminal-img {
    width: 40px;
    height: 40px;
}
.terminal-img-text {
    font-size: 13px;
    font-weight: bold;
    color: #1296db;
}